import React from 'react'
import { useRouter } from 'next/router'
import TplProject from '../../components/templates/template-project'
import PostTile1 from '../../components/tiles/posts/tile1'
import PostTile2 from '../../components/tiles/posts/tile2'
import PostTile3 from '../../components/tiles/posts/tile3'
import TileAvailable from '../../components/tiles/posts/tileAvailable'
import TileFeatured from '../../components/tiles/posts/tileFeatured'
import {getMockProject} from '../../components/mocks/mocks'

const Project = () => {
    const router = useRouter()
    const { projectid } = router.query
    const project = getMockProject(projectid);
    console.log('router.query', router.query);

    return (

        <TplProject 
            title='History of Medicine : Who is Galen the physician?' 
            info={'INFO'}>
            
            <div className="row">
                
                <PostTile1
                    title="About"
                    text="To raise awareness about juvenile diabetes, I attempt to identify what helps avoid extreme sugar level changes when I exercise. A single person study (N=1) with daily monitoring of glucose levels that I will do for at least 1 year. I expect to observe that my values will depend on what I eat and when I eat it."
                    selected
                    />
               
                <PostTile2
                    title="Basic facts about Galen"
                    text="Born: 129 AD (Pergamon, Asia, Roman Empire) ##Died: c. 210 AD (Rome, Roman Empire) ##Nationality: Greek ##Occupation: Physician"
                    img="img2.png"
                />
                
                
            </div>
            <div className="row ">
                <div className="row colspan">
                    <PostTile1
                        title="Diabetes at 8 years old"
                        text="In this podcast I am sharing the story of the very first moments that I got very sick from high blood glucose."
                        podcast
                        />

                    <TileFeatured 

                    />

                    
                    <PostTile2
                        title=" Basic facts about Galen"
                        text="Introduction to Galen, his travels as a young man and how important Alexandria, Egypt was to his work."
                        videoId="vXbf1i1NyiQ"
                        className="last"
                    />
                </div>
                <PostTile3 
                    title="Fun facts"
                    text="Galen collected all his statements without ever dissecting a human body."
                    img="img3.png"
                />
            </div>
            <div className="row">
               <TileAvailable />
               <TileFeatured />
               <TileAvailable />
            </div>

           

            


        <style jsx> 
        {`

        .colspan .cell {
            margin-bottom: 11px;
        }

        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: inherit;
        }

        .cell:last-of-type {
            margin-right: 0px;
        }
        .row:last-of-type {
            margin-bottom: 0px;
        }
        .cell {
            background-color: #F4F6F6;
            border: 1px solid #F0F2F2;
            border-radius: 5px;
            margin-right: 11px;
            height: 282px;

            color: #707070;
            letter-spacing: 0;
        }

        .selected {   
            background-color: #D3DDDD;
            border: 1px solid #4E7876;
            color: #000000;
            opacity: 0.5;
        }

        .cell:hover {
            
            opacity: 1;
        }

        .cell.size1 {
            width: 325px;
        }

        .cell.size2 {
            width: 663px;
        }

        .cell.size3 {
            width: 325px;
            height: 577px;
        }

        .cell.featured {
            border: 1px solid #C2CCCC;
        }
        .featured .header {
            border-bottom: 2px solid #D3DDDD;
        }

        .cell.available {
            border: 2px dashed #C2CCCC;
            background-color: #fff; 
            width: 323px;
            height: 280px;
        }
        
        .available .header {
            border-bottom: 2px dashed #D3DDDD;
        }

        .row {
            display: flex;
            margin-bottom: 11px;
        }
        
        .row.colspan { 
            flex-flow: wrap;
            width: 676px;
            margin-bottom: 0px !important;
        }

        .row.colspan .cell { 
            margin-bottom: 11px;
        }

        .last { 
            margin-bottom: 0px !important;
        }

        .header, .body, .footer {
            padding: 0px 28px 0px 28px;
        }


        .body {
            margin-top: 13px;
            font: 12px/17px Arial;
        }

        .actions {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
        }

        .footer {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;

            height: 40px;
            border-bottom: 2px solid #FFFFFF;
            
            font: Bold 16px/19px Arial;
        }

        `}
            
        </style>

        </TplProject>
    )
}

Project.getInitialProps = async () => {
    return {};
  };

export default Project;